import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { DatePickerModule } from '@syncfusion/ej2-ng-calendars';
import { GridModule } from '@syncfusion/ej2-ng-grids';
import { DialogModule } from '@syncfusion/ej2-ng-popups';
import { AlertnotificationComponent } from './alertnotification/alertnotification.component';

@NgModule({
  imports: [
      CommonModule,
      FormsModule,
      GridModule,
      NgbModule,
      DatePickerModule,
      DialogModule
  ],
  declarations: [AlertnotificationComponent],
  exports: [
      AlertnotificationComponent
  ],
  providers: [AlertnotificationComponent]
})
export class SharedModule { }
