import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { HttpErrorHandlerService, HandleError, HandleSuccess } from './http-error-handler.service';
import { environment } from '../../environments/environment';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

export class UserRolesMenuSubOptionsMapping {
  Id: number;
  UserRoleId: number;
  UserRoleName: string;
  CreatedOnUtc: any;
  UpdatedOnUtc: any;
  MenuSubOptionId: number;
  MenuSubOptionName: string;
  DeleteAccess: boolean;
  CreateAccess: boolean;
  UpdateAccess: boolean;
  ReadAccess: boolean;
  errorMessage: string;
  errorStatus: boolean;
}

@Injectable()
export class UserrolesmenusuboptionsmappingService {

  private apiUrl = environment.api + "/userrolesmenusuboptionsmapping";
  private handleError: HandleError;
  private handleSuccess: HandleSuccess;

  constructor(private http: HttpClient,
    HttpErrorHandlerService: HttpErrorHandlerService) {
    this.handleError = HttpErrorHandlerService.createHandleError('UserRolesMenuSubOptionsMappingService');
    this.handleSuccess = HttpErrorHandlerService.createHandleSuccess('UserRolesMenuSubOptionsMappingService');
  }

  listFiltered(id: number): Observable<UserRolesMenuSubOptionsMapping[]> {
    return this.http.get<UserRolesMenuSubOptionsMapping[]>(this.apiUrl + "/listFiltered/" + id)
      .pipe(
      map((result: any) => { return this.handleSuccess("listFiltered", result) }),
      catchError(this.handleError<UserRolesMenuSubOptionsMapping[]>(`listFiltered id=${id}`))
      );
  }
  listAll(): Observable<UserRolesMenuSubOptionsMapping[]> {
    return this.http.get<UserRolesMenuSubOptionsMapping[]>(this.apiUrl + "/listAll")
      .pipe(
      map((result: any) => { return this.handleSuccess("listAll", result) }),
      catchError(this.handleError('listAll', []))
      );
  }
  get(id: number): Observable<UserRolesMenuSubOptionsMapping[]> {
    return this.http.get<UserRolesMenuSubOptionsMapping[]>(this.apiUrl + "/get/" + id)
      .pipe(
      map((result: any) => { return this.handleSuccess("get", result) }),
      catchError(this.handleError<UserRolesMenuSubOptionsMapping[]>(`get id=${id}`))
      );
  }
  create(item: UserRolesMenuSubOptionsMapping): Observable<UserRolesMenuSubOptionsMapping> {
    return this.http.post<UserRolesMenuSubOptionsMapping>(this.apiUrl + "/create", item, httpOptions)
      .pipe(
      map((result: any) => { return this.handleSuccess("create", result) }),
      catchError(this.handleError<UserRolesMenuSubOptionsMapping>('create'))
      );
  }
  update(item: UserRolesMenuSubOptionsMapping): Observable<any> {
    return this.http.post(this.apiUrl + "/update", item, httpOptions)
      .pipe(
      map((result: any) => { return this.handleSuccess("update", result) }),
      catchError(this.handleError<any>('update'))
      );
  }
  delete(item: UserRolesMenuSubOptionsMapping | number): Observable<UserRolesMenuSubOptionsMapping> {
    const id = typeof item === 'number' ? item : item.Id;
    const url = `${this.apiUrl}/delete/${id}`;
    return this.http.post<UserRolesMenuSubOptionsMapping>(url, httpOptions)
      .pipe(
      map((result: any) => { return this.handleSuccess("delete", result) }),
      catchError(this.handleError<UserRolesMenuSubOptionsMapping>('delete'))
      );
  }
}
